<template>
  <div>
    <div class="music-player-container" :class="{'is-playing': isPlaying}">

      <div class="music-player">
        <div class="player-content-container">
          <h1 class="artist-name">{{ currentMusic.artist }}</h1><!--  /.track-title -->
          <h2 class="album-title">{{ currentMusic.album }}</h2><!--  /.album-title -->
          <h3 class="song-title">{{ currentMusic.title }}</h3><!--  /.song-title -->
          <span style="display: flex;justify-content: center;margin-bottom: 1rem">{{
              currentTime + '/' + durationTime
            }}</span>
          <div class="music-player-controls">
            <div class="control-back" @click="prev"></div><!--  /.control-back -->
            <div class="control-play" @click="play"></div><!--  /.control-play -->
            <div class="control-forwards" @click="next"></div><!--  /.control-forwards -->
          </div><!--  /.musics-player-controls -->
        </div><!--  /.player-content-container -->
      </div><!--  /.musics-player -->

      <div class="album">
        <div class="album-art" :style="`background: #fff url(${currentMusic.photo}) center/cover no-repeat;`"></div>
        <!--  /.album-art -->
        <div class="vinyl" :style="`background-image:url('./musics/vinyl.png'),
      url(${currentMusic.photo})`"></div>
        <!--  /.vinyl -->
      </div><!--  /.album-art -->

    </div>
  </div>

</template>
<script>
import musics from "@/musics";

export default {
  name: "BaseMusic",
  data() {
    return {
      isPlaying: false,
      currentMusic: null,
      audio: new Audio(),
      currentMusicIndex: 0,
      length: musics.length,
      currentTime: null,
      durationTime: 0
    }
  }, methods: {
    generateTime() {
      let durMin = Math.floor(this.audio.duration / 60);
      let durSec = Math.floor(this.audio.duration - durMin * 60);

      let curMin = Math.floor(this.audio.currentTime / 60);
      let curSec = Math.floor(this.audio.currentTime - curMin * 60);

      if (durMin < 10) {
        durMin = '0' + durMin;
      }
      if (durSec < 10) {
        durSec = '0' + durSec;
      }
      if (curMin < 10) {
        curMin = '0' + curMin;
      }
      if (curSec < 10) {
        curSec = '0' + curSec;
      }

      this.durationTime = `${durMin}:${durSec}`;
      this.currentTime = `${curMin}:${curSec}`;
    },
    play() {
      if (this.isPlaying) {
        this.audio.pause();
        this.isPlaying = false;
      } else {
        this.audio.play();
        this.isPlaying = true;
      }
    },
    changeCurrentMusic() {
      // this.currentMusic = musics[0];
      this.currentMusic = musics[this.currentMusicIndex];
      this.audio.src = this.currentMusic.source;
      if (this.isPlaying) {
        this.audio.play();
      }
    },
    next() {
      if (this.currentMusicIndex === length - 1) {
        this.currentMusicIndex = 0;
      } else {
        this.currentMusicIndex++;
      }
      this.changeCurrentMusic();
    }
    , prev() {
      if (this.currentMusicIndex === 0) {
        this.currentMusicIndex = length - 1;
      } else {
        this.currentMusicIndex--;
      }
      this.changeCurrentMusic();
    }
  }, created() {
    this.audio.onloadedmetadata = () => {
      this.generateTime();
    }
    this.audio.ontimeupdate = () => {
      this.generateTime();
    }
    this.changeCurrentMusic();

  }

}
</script>

<style scoped lang="scss">
@import "../assets/musicstyles.css";
</style>